import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter, Input } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { InfoNutricionalService } from './../info-nutricional.service';
import { InfoNutricionalDTO } from 'models/domain/info-nutricional.dto';

@Component({
  selector: 'info-nutricional-popup-modal',
  templateUrl: './info-nutricional-popup-modal.component.html',
  styleUrls: ['./info-nutricional-popup-modal.component.scss']

})
export class InfoNutricionalPopupModalComponent extends AbstractPopupComponent implements OnInit {


  item: InfoNutricionalDTO;


  constructor(
    public infoService: InfoNutricionalService,
    public fb: FormBuilder) {
    super('infoNutricionalPopup');
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.criarForm();
    this.showPopup("Cadastra informação nutricional");
  }

  showAlteracao(item: InfoNutricionalDTO) {
    this.alterarForm(item)
    this.showPopup("Alterar informação nutricional");

  }

  btnConfirmar(item: InfoNutricionalDTO) {
    this.acao(item, this.infoService);
  }

  criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      nmInfoNutricional: ['', Validators.required],
      emAlteracao: [false]
    })
  }
  

  alterarForm(item: InfoNutricionalDTO) {
    this.formulario.patchValue({
      id: item.id,
      nmInfoNutricional: item.nmInfoNutricional,
      emAlteracao: true
    })
  }

}



