import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';


import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { UnidadeMedidaDTO } from './../../../../../models/domain/unidade-medida.dto';
import { InfoNutricionalService } from './../info-nutricional.service';
import { InfoNutricionalDTO } from 'models/domain/info-nutricional.dto';
import { TrataErrorService } from 'services/trata-error.service';


@Component({
  templateUrl: './listar-info-nutricional.component.html',
  styleUrls: ['./listar-info-nutricional.component.scss']
})
export class ListarInfoNutricionalComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeSatusModal') popupChangeStatus;

  item: InfoNutricionalDTO;
  items: InfoNutricionalDTO[];

  constructor(
    public infoService: InfoNutricionalService,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos();
  }

  selecionarItem(item: InfoNutricionalDTO) {
    this.item = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: InfoNutricionalDTO) {
    this.item = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.infoService.changeStatus(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Informação Nutricional', 'O status da informação nutricional foi alterado com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  excluir() {
    this.infoService.excluir(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Informação Nutricional', 'A informação nutricional foi excluída com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  private carregarTodos() {
    this.infoService.findAll().subscribe(response => {
      this.items = response
    }, error => TrataErrorService.tratarError(error));
  }

}



